export default {
	data() {
		return {
			userInfo: {
				nickName: '',
				city: '',
				province: '',
				avatarUrl: ''
			}
		}
	},
	
	computed: {
		getAvatar() {
			return `background-image:url(${this.userInfo.avatarUrl})`
		}
	},
	
	methods: {
		goIframe(router) {
			const url = '/pages/iframe/' + router
			console.log(url)
			uni.navigateTo({
				url
			})
		}
	},
	
	onLoad() {
		console.log(getApp())
		const THAT = this
		wx.getSetting({
			success(res)	{
				console.log(res)
				THAT.userInfo = res
			}
		})
		
		wx.login({
			success(res) {
				console.log(res)
			}
		})
	}
}
